.social {
  width: 10%;
  position: relative;

  @media (max-width: $tablet-width) {
    width: 20%;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
  }
}

.social__list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.social__item {
  margin-bottom: 30px;
  width: 100%;

  @media (max-width: $mobile-width) {
    width: auto;
    margin: 0 15px 0 0;
  }
}

.social__link {
  font-family: $font-family-text;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: $basic-white;
  text-decoration: none;
}

.social__link span {
  @media (max-width: $mobile-width) {
    display: none;
  }
}
